html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
  user-select: none;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}
body.body-fixed {
  overflow: hidden;
}
@media (min-width: 580px) {
  body.body-scroll {
    padding-top: 0px;
  }
}
body::-webkit-scrollbar {
  max-width: 10px;
  background: #520606;
}
body::-webkit-scrollbar-thumb {
  width: 100%;
  background: #aaa;
  border-top-right-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
}

h1, h2, h3, h4 {
  margin: 0 0 5rem 0;
  font-family: "Open Sans", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

a {
  text-decoration: none;
  font-family: "Open Sans", sans-serif;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

input.input-correct {
  border-bottom: #fff;
  color: #fff;
}

input.input-error {
  border-bottom: #F53756;
  color: #F53756;
}

label.label-correct {
  color: #fff;
}

label.label-error {
  color: #F53756;
}

.disfocus {
  display: block;
  position: absolute;
  z-index: 99;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3px);
}

#fullname-error,
#user-error,
#email-error,
#passconfirm-error,
#pass-error,
#input-error {
  display: none;
  width: 100%;
  margin-top: 1rem;
  color: #d00;
  font-size: 1.4rem;
  text-align: center;
}

#proximamente {
  background: rgba(0, 0, 0, 0.02);
  backdrop-filter: blur(2px);
  width: 100%;
  padding: 3rem;
}
#proximamente h3 {
  font-family: "Roboto", sans-serif;
  font-size: 5rem;
  color: #333;
  font-weight: 400;
  text-align: center;
}
#proximamente p {
  text-align: center;
  color: #333;
  font-size: 2rem;
}

@keyframes wave {
  0%, 100% {
    clip-path: polygon(0% 60%, 10% 55%, 20% 53%, 30% 55%, 40% 60%, 50% 63%, 60% 65%, 70% 66%, 80% 65%, 90% 64%, 100% 63%, 100% 100%, 0% 100%);
  }
  50% {
    clip-path: polygon(0% 50%, 10% 55%, 20% 60%, 30% 65%, 40% 63%, 50% 60%, 60% 58%, 70% 56%, 80% 55%, 90% 59%, 100% 70%, 100% 100%, 0% 100%);
  }
}
@keyframes wave2 {
  0%, 100% {
    clip-path: polygon(0% 50%, 20% 45%, 40% 50%, 60% 55%, 80% 55%, 90% 54%, 100% 53%, 100% 100%, 0% 100%);
  }
  50% {
    clip-path: polygon(0% 60%, 20% 50%, 40% 40%, 60% 45%, 80% 50%, 90% 55%, 100% 63%, 100% 100%, 0% 100%);
  }
}
.flash-errors {
  background: #d00;
  border: none;
  border-top: 4px solid #fff;
  border-bottom: 4px solid #fff;
  padding: 2rem;
  margin-top: 1.5rem;
}
.flash-errors span {
  display: block;
  width: 100%;
  font-family: "Inconsolata", "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  color: #fff;
}

.navbar {
  width: 100%;
  height: 10rem;
  background: #64042e;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  position: relative;
}
.navbar .navbar-filter {
  z-index: -10;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1);
}
.navbar .header {
  z-index: 999;
  width: 100%;
}
.navbar #logged_user {
  position: absolute;
  width: 100%;
  background: #101010;
  margin: 0;
  padding: 0.5rem;
  text-align: right;
  font-family: "Inconsolata", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
}

.header nav.header__nav {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 0;
  background: #64042e;
  z-index: 1000;
}
.header nav.header__nav .dropmenu-box {
  display: none;
}
@media (max-width: 768px) {
  .header nav.header__nav .dropmenu-box {
    display: initial;
  }
}
.header nav.header__nav .dropmenu-box #dropmenu {
  width: 100%;
  background: transparent;
  border: none;
  padding: 1.5rem;
  color: #fff;
  text-align: start;
  transition: all ease 0.5s;
  cursor: pointer;
}
.header nav.header__nav .dropmenu-box #dropmenu:hover {
  background: #000;
}
.header nav.header__nav ul#nav_menu {
  width: min(100%, 144rem);
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .header nav.header__nav ul#nav_menu {
    background: #64042e;
    flex-direction: column;
  }
  .header nav.header__nav ul#nav_menu.dropmenu-disable {
    display: none;
  }
  .header nav.header__nav ul#nav_menu.dropmenu-active {
    display: block;
  }
}
.header nav.header__nav ul#nav_menu li {
  display: inline-block;
  width: fit-content;
  padding: 0;
  margin: 0;
}
@media (max-width: 768px) {
  .header nav.header__nav ul#nav_menu li {
    display: block;
    width: 100%;
  }
}
.header nav.header__nav ul#nav_menu li a {
  width: 100%;
  padding: 2rem;
  display: inline-block;
  color: #fff;
  font-family: "Inconsolata", "Roboto", sans-serif;
  transition: all ease 0.5s;
}
.header nav.header__nav ul#nav_menu li a:hover {
  background: #000;
  color: #fff;
}
@media (max-width: 768px) {
  .header nav.header__nav ul#nav_menu li a {
    text-align: center;
  }
}
.header nav.header__nav.navbar_fixed {
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: fit-content;
  position: fixed;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.7);
  z-index: 200;
}

.content {
  width: 100%;
  height: 100%;
  position: relative;
}

.contetn-profile,
.contetn-work_test,
.content-about,
.content-index,
.content-register,
.content-login {
  width: 100%;
  height: 100%;
  min-height: 70rem;
  padding-top: 5rem;
  background: transparent;
  position: relative;
}

.content-index {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5rem;
  padding-bottom: 10rem;
}
@media (max-width: 992px) {
  .content-index {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.content-index .index__links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
  margin: -10rem 0 4rem 0;
}
.content-index .index__links .item {
  width: min(90%, 60rem);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: transparent;
  backdrop-filter: blur(2px);
}
@media (max-width: 580px) {
  .content-index .index__links .item {
    flex-direction: column;
  }
}
.content-index .index__links .item:hover {
  background: rgba(0, 0, 0, 0.4);
}
.content-index .index__links .item .item-info h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 1rem;
  padding-left: 2rem;
}
.content-index .index__links .item .item-info p {
  margin: 0;
  padding: 1rem;
  padding-left: 2rem;
  font-family: "Inconsolata", "Roboto", sans-serif;
}
.content-index .index__links .item .item-btn {
  height: 16.5rem;
}
@media (max-width: 580px) {
  .content-index .index__links .item .item-btn {
    width: 100%;
    height: fit-content;
  }
}
.content-index .index__links .item .item-btn a.btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: rgb(15, 50, 116);
  padding: 1.5rem;
  transition: all ease 0.3s;
}
.content-index .index__links .item .item-btn a.btn:hover {
  background: #000;
  color: #fff;
}

.content-index .banner {
  width: 100%;
  padding: 1.5rem 4rem;
  background: #64042e;
  box-shadow: 0.2rem 0 2rem #000 inset;
}
.content-index .banner p {
  color: #fff;
  font-family: "Inconsolata", "Roboto", sans-serif;
}
.content-index .banner p a {
  color: #ccc;
  font-size: 1.4rem;
}
.content-index .banner p a:hover {
  text-decoration: underline;
}

.content-index .login {
  background: rgba(0, 0, 0, 0.95);
  width: min(90%, 40rem);
  padding: 1.5rem;
  border-radius: 1rem;
}
.content-index .login picture img {
  display: block;
  margin: 1rem auto;
  max-width: 12rem;
  max-height: 20rem;
}

.alert .panic-button {
  background: rgb(175, 8, 8);
  padding: 1rem;
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
}
.alert .panic-button .circle-contour {
  width: 100%;
  height: 100%;
  padding: 0.1rem;
  background: rgb(80, 3, 3);
  border-radius: 50%;
}
.alert .panic-button .circle-contour:hover {
  padding: 0.5rem;
}
.alert .panic-button .circle-contour:hover .circle-button span {
  font-size: 2.5rem;
}
.alert .panic-button .circle-contour:active {
  padding: 0.7rem;
}
.alert .panic-button .circle-contour:active .circle-button span {
  font-size: 2.3rem;
}
.alert .panic-button .circle-contour .circle-button {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 1rem;
  background: rgb(211, 9, 9);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.alert .panic-button .circle-contour .circle-button span {
  position: absolute;
  color: #fff;
  font-family: "Inconsolata", "Roboto", monospace, sans-serif;
  font-size: 3rem;
  font-weight: 500;
}

.content-index .menu {
  background: rgb(255, 255, 255);
  padding: 1.5rem;
  border-radius: 1rem;
  max-height: 55rem;
  overflow-wrap: break-word;
  overflow-y: scroll;
  box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.4);
}
.content-index .menu::-webkit-scrollbar {
  max-width: 0.7rem;
  background: none;
  border-radius: 0.5rem;
}
.content-index .menu::-webkit-scrollbar-thumb {
  width: 100%;
  background: rgb(90, 9, 9);
  border-radius: 0.35rem;
}
@media (max-width: 768px) {
  .content-index .menu {
    width: min(95%, 40rem);
  }
}
.content-index .menu h3 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.content-index .menu h3 picture img {
  max-width: 10rem;
}
.content-index .menu h3 span {
  font-family: "Inconsolata", "Roboto", sans-serif;
  font-weight: 700;
  font-size: 3rem;
  color: #520606;
  padding-left: 0.5rem;
}
.content-index .menu ul {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.content-index .menu ul li {
  width: 100%;
  margin: 0;
  padding: 0;
}
.content-index .menu ul li a {
  display: block;
  padding: 1.5rem;
  text-align: center;
  border-radius: 0.8rem;
  transition: all ease 0.3s;
  color: #333;
}
.content-index .menu ul li a:hover {
  background: #520606;
  color: #fff;
}

.content-login {
  width: 100%;
  height: 100%;
  padding: 10% 5%;
}

.content-login .login {
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.95);
  width: min(90%, 40rem);
  padding-top: 1.5rem;
  border-radius: 1rem;
}
@media (max-width: 768px) {
  .content-login .login {
    margin: 0 auto;
  }
}
.content-login .login picture img {
  display: block;
  margin: 1rem auto;
  max-width: 12rem;
  max-height: 20rem;
}

.login__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 3rem;
}
.login__form .login__form-group {
  width: 100%;
  margin: 1.5rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.login__form .login__form-group label {
  width: 100%;
  padding: 0.5rem 0.5rem 1.5rem 0.5rem;
  color: #fff;
}
.login__form .login__form-group input {
  width: min(100%, 120rem);
  margin: 0;
  padding: 1.5rem;
  background: transparent;
  border: none;
  border-bottom: 0.4rem solid #fff;
  text-align: center;
  text-align-last: center;
  color: #fff;
}
.login__form .login__form-group input::placeholder {
  color: #aaa;
}
.login__form .login__form-group input:focus {
  background: linear-gradient(to top, #777 0% 0.5%, transparent 10% 30%, transparent 30% 100%);
}
.login__form .login__form-group input.error:focus {
  background: linear-gradient(to top, #d00 0% 0.5%, transparent 10% 30%, transparent 30% 100%);
}
.login__form .login__form-buttons {
  width: 100%;
}
.login__form .login__form-buttons .btn {
  display: block;
  width: 100%;
  background: #520606;
  color: #fff;
  margin: 1.5rem auto;
  padding: 1rem;
  text-align: center;
  border-radius: 0.5rem;
}
.login__form .login__form-buttons .btn:hover {
  background: #000;
}
.login__form .login__form-buttons .btn:nth-child(1) {
  background: #fff;
  color: #333;
}
.login__form .login__form-buttons .btn:nth-child(1):hover {
  background: #000;
}
.login__form .login__form-forgot_pass {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  margin: 0;
  padding: 0;
}
.login__form .login__form-forgot_pass a {
  display: block;
  font-size: 1rem;
  color: #fff;
}
.login__form .login__form-forgot_pass a:hover {
  color: #ccc;
}

.login__form-btn1 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border: none;
  cursor: pointer;
  transition: 0.3s ease all;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}
.login__form-btn1 span {
  position: relative;
  transition: 0.3s ease all;
}
.login__form-btn1 svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  fill: none;
}
.login__form-btn1 svg rect {
  width: 100%;
  height: 100%;
  stroke: rgb(97, 14, 25);
  stroke-width: 10px;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  transition: 0.6s ease all;
}
.login__form-btn1:hover {
